define('aaronpetcoff/serializers/repository', ['exports', 'ember-data', 'ember'], function (exports, DS, Ember) {

  'use strict';

  exports['default'] = DS['default'].JSONAPISerializer.extend({
    keyForAttribute: function keyForAttribute(key) {
      return Ember['default'].String.decamelize(key);
    },

    normalizeQueryResponse: function normalizeQueryResponse(store, model, payload) {
      var response = {};

      response.data = payload.items.map(function (repo) {
        /**
         * data about each talk is stored in the repository's
         * description in a comma separated list
        */
        var description = repo.description.split(', '),
            // break desc into array

        // assign properties
        title = description[1],
            location = description[2],
            date = new Date(description[3]),
            status = description[4],
            slides = undefined; // optional property in case slides aren't HTML

        if (description[5]) {
          slides = description[5];
        }

        return {
          id: repo.id,
          type: model.modelName,
          attributes: {
            title: title,
            location: location,
            date: date,
            url: 'http://ughitsaaron.github.io/' + repo.name,
            slides: slides || '',
            status: status
          }
        };
      });

      return {
        data: response.data
      };
    }
  });

});